import React from 'react'
import { TabBar } from 'antd-mobile';

const Spp = (props) => {
    return (
        <div>
            <button onClick={() =>
                props.history.goBack()
            }
            style={{
                marginTop:'30px',
                position:'fixed'
            }}>{`<<=`}</button>
            <img 
            style={{
                margin:'70px',
                position:'fixed'  
            }}
            src='https://img.alicdn.com/imgextra/i1/18674716/O1CN01TVSQTT1khyTdMzxLg_!!0-saturn_solar.jpg_220x220.jpg_.webp'></img>
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <div style={{float:'left',
                    width:'50%'}}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        icon={
                            <i className='iconfont icon-dianpu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu'></i>
                        }
                        onPress={() => {
                            props.history.push('/cart');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="客服"
                        icon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        onPress={() => {
                            props.history.push('/list');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="收藏"
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        onPress={() => {
                            props.history.push('/list');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                </TabBar></div>
                <div style={{width:'50%',float:"right",
                            backgroundColor:'rgb(247, 163, 38)'}}>
                    <TabBar>
                    <TabBar.Item
                        title="加入购物车"
                        icon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        onPress={() => {
                            props.history.push('/cart');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="立即购买"
                        icon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        onPress={() => {
                            props.history.push('/list');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    </TabBar>
                </div>
            </div>
        </div>
    )
}

export default Spp
